@import url('https://fonts.googleapis.com/css2?family=Dongle&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

.service-card {
    background-color: rgba(168, 135, 201, 0.7); /* Lower opacity background */
    height: 150px;
    width: 150px;
  
    margin: 0.5rem 1rem;
    border-radius: 0.75rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    position: relative;
    transition: all 0.3s ease-in-out;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .service-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.3s ease-in-out;
  }
  
  .service-img {
    margin-top: 40px;
    height: 5rem;
    width: 5rem;
    transition: all 0.3s ease-in-out;
  }
  
  .service-heading {
    font-family: 'Dongle', sans-serif;
    font-size: 45px;
    transition: all 0.3s ease-in-out;
  }
  
  .service-description {
    font-family: 'Dongle', sans-serif;
    font-size:25px;
    opacity: 0;
    line-height: 16px;
    transition: opacity 0.3s ease-in-out;
    margin-left: 1rem;
    margin-right: 1rem;
  }
  
  .service-card:hover .service-img,
  .service-card:hover .service-heading {
    transform: scale(0.8);
  }
  
  .service-card:hover .service-description {
    opacity: 1;
  }
  
  .project-card:hover img {
    transform: scale(1.05);
  }
  
  .project-card:hover .project-content {
    opacity: 1;
  }
  
  .project-card:hover .overlay {
    background-color: rgba(47, 79, 79, 0.5); /* darkslategrey with 50% opacity */
  }
  
  .overlay {
    transition: background-color 0.3s ease-in-out;
  }
  
  .project-content {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
  
  